.App {
	font-family: sans-serif;
	margin: 0;
	height: 100%;
}

header {
	background-color: white;
	padding-bottom: 10px;
}

.Profile-Main-Logo {
	height: 400px;
	padding: 0 15px 15px 10px;
	float: left;
	margin-top: 5%;
}

.Profile-Name {
	font-size: 1.6rem;
	font-weight: bold;
	margin-bottom: 1rem;
	color: black;
}

.App-logo {
	float: right;
	height: 400px;
	padding: 0 15px 15px 10px;
}

.App-intro {
	font-size: 1.3rem;
	width: 50%;
	text-align: left;
	float: right;
	margin-top: 5%;
	color: Grey;
}
.About-Me-Content {
	float: left;
	font-size: 1.1rem;
	width: 50%;
	text-align: left;
	line-height: 1.7rem;
}

.Section {
	margin: 2rem;
	border-spacing: 20px;
	display: inline-block;
}

.Projects-Section {
	margin-top: 1rem;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
}

.App-side {
	font-weight: bolder;
	color: DarkSlateGray;
	font-size: 1.5rem;
	text-align: left;
	margin-bottom: 1rem;
}

.Project-image {
	height: 80%;
	width: 90%;
}

.Project-image:hover {
	-webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.Projects-Description {
	color: DarkSlateGray;
	display: grid;
	grid-template-columns: 3fr 4fr;
}
.Project-details {
	margin: 5rem;
	font-size: 1.1rem;
	text-align: left;
	line-height: 1.7rem;
}

.Contact-info {
	font-size: 15px;
	background-color: #ecf0f1;
}

.Contact-options {
	padding-top: 15px;
	padding-bottom: 15px;
	opacity: 0.95;
	position: fixed;
	z-index: 100;
	-webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	background-color: #ecf0f1;
	width: 100%;
}

.Other-Links:link,
.Other-Links:visited,
.Other-Links:active {
	color: DarkSlateGray;
	text-decoration: none;
	text-align: center;
	padding: 0.5%;
	font-weight: bolder;
	font-size: 15px;
	margin-left: 3px;
	margin-right: 3px;
	background: #ffffff;
	text-align: center;
	border-radius: 20px;
}

.button {
	display: block;
	width: 115px;
	height: 25px;
	background: #4e9caf;
	padding: 10px;
	text-align: center;
	border-radius: 5px;
	color: white;
	font-weight: bold;
}
.Other-Links:hover {
	color: #979a9a;
	text-decoration: none;
	text-align: center;
}

a:link,
a:visited,
a:active {
	color: DarkSlateGray;
	text-decoration: none;
	text-align: center;
	font-weight: bolder;
	font-size: 25px;
	padding-top: 10px;
	padding-right: 5px;
}

a:hover {
	color: #979a9a;
	text-decoration: none;
	text-align: center;
}
ul {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
}
li {
	text-align: left;
	margin: 0.5rem;
	font-size: 1.1rem;
	line-height: 1.7rem;
}

.fa {
	font-size: 200px;
	margin-left: 1rem;
	color: DarkSlateGray;
}

